@import url(https://fonts.googleapis.com/css?family=Sriracha&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header {
    font-family: 'Sriracha', cursive;
    min-height: 10vh;
    width: 100%;
    background: -webkit-gradient(linear,left top, left bottom,from(#f53d2d),to(#f63));
    background: linear-gradient(-180deg,#f53d2d,#f63);
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Header #logo {
    width: 7.5vh;
    margin-right: 1rem;
}

.Header #brand {
    color: white;
    font-size: 1.5rem;
}

@media screen and (max-width: 768px) {
    .Header #brand {
        font-size: 1.2rem;
    }
}
.Main {
    background-color: white;
    width: 100%;
    min-height: 82vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.GetUris {
    width: 85%;
    margin-bottom: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.GetUris form {
    position: relative;
    width: 100%;
    margin: 2rem 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.GetUris input {
    width: 75%;
    height: 2.5rem;
    background-color: rgba(0, 0, 0, 0.05);
    border: none;
    padding: 0 3rem 0 1.5rem;
    font-size: 1.05rem;
    border-radius: 1rem;
    outline: none!important;
    color: rgba(0, 0, 0, 0.5);
}

.GetUris .get {
    cursor: pointer;
    width: 4rem;
    height: 2.5rem;
    margin: 1rem 0 1rem 0;
    border-radius: 0.25rem;
    border: none;
    background-color: #28a745;
    color: white;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 1rem;
    outline: none!important;
}

.GetUris .get:hover {
    background-color: #1d8d37;
}

.GetUris .result {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.GetUris .result div {
    width: 20%;
    margin: 10px 10px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.3);
}

.GetUris .result img {
    width: 100%;
    height: auto;
}

.GetUris .Loading {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.GetUris .Loading div {
    font-size: 1rem;
    color: rgb(117, 117, 117);
}

.GetUris #clear {
    background-color: rgb(145, 145, 145);
    width: 1rem;
    height: 1rem;
    position: absolute;
    right: 10.7%;
    top: 0.75rem;
    border-radius: 0.5rem;
    cursor: pointer;
}

.GetUris #clear:hover {
    background-color: rgb(112, 112, 112);
}

.GetUris #line-1 {
    width: 0.625rem;
    height: 0.125rem;
    background-color: white;
    -webkit-transform: translate(0.1875rem, 0.4375rem) rotate(45deg);
            transform: translate(0.1875rem, 0.4375rem) rotate(45deg);
}

.GetUris #line-2 {
    width: 0.625rem;
    height: 0.125rem;
    background-color: white;
    -webkit-transform: translate(0.1875rem, 0.3125rem) rotate(-45deg);
            transform: translate(0.1875rem, 0.3125rem) rotate(-45deg);
}

.GetUris #notice {
    right: 0;
    top: 0;
}

.GetUris #notice button {
    background-color: #6c757dc9;
    color: white;
    font-size: 1rem;
    padding: 0.75rem;
    border-radius: 0.25rem;
    border: none;
    cursor: pointer;
    margin-bottom: 1rem;
}

.GetUris #notice button:hover {
    background-color: #6c757d;
}

@media screen and (max-width: 1000px) {
    .GetUris .result div {
        width: 30%;
    }
}

@media screen and (max-width: 768px) {
    .GetUris {
        width: 100%;
    }

    .GetUris .result div {
        width: 40%;
    }
}

@media screen and (max-width: 400px) {
    .GetUris .result div {
        width: 90%;
    }
}

.loader {
    margin-bottom: 1rem;
    border: 7px solid #f3f3f3;
    border-radius: 50%;
    border-top: 7px solid rgb(148, 148, 148);
    border-bottom: 7px solid rgb(148, 148, 148);
    border-right: 7px solid rgba(148, 148, 148, 0.377);
    border-left: 7px solid rgba(148, 148, 148, 0.377);
    width: 50px;
    height: 50px;
    -webkit-animation: spin 0.7s linear infinite;
    animation: spin 0.7s linear infinite;
}
  
@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
}
  
@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.Footer {
    min-height: 8vh;
    width: 100%;
    background: #e7e7e7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: rgba(0, 0, 0, 0.65);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Footer div {
    margin: 0 1rem;
}

.App {
  font-family: 'Poppins', sans-serif;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
